import getUniqueString from '../../utils/getUniqueString';

const UPNG = require('upng-js');
const download = require('downloadjs');
const GIF = require('gif.js-upgrade');

export default class DownloadControl {
  constructor(getFrameStack, getFPS) {
    this.getFrameStack = getFrameStack;
    this.getFPS = getFPS;
    this.saveGIF = this.saveGIF.bind(this);
    this.saveAPNG = this.saveAPNG.bind(this);
    this.setFileName = this.setFileName.bind(this);
    this.getFileName = this.getFileName.bind(this);
    this.gifDownload = this.gifDownload.bind(this);
    this.fileName = '';
  }

  setFileName(event) {
    this.fileName = event.target.value;
  }

  saveGIF() {
    const mainCanvas = document.getElementById('canvas');
    const gif = new GIF({
      workers: 2,
      workerScript: './gif.worker.js',
      quality: 10,
      width: mainCanvas.width,
      height: mainCanvas.height, 
    });
    
    const frameStack = this.getFrameStack();
    const frameDelay = 1000 / this.getFPS();
    frameStack.forEach(element => {
      const img = new Image;
      img.src = element;
      gif.addFrame(img, {delay: frameDelay});
    });
    
    gif.on('finished', this.gifDownload);
    
    gif.render();
  }

  gifDownload(blob) {
    const fileName = this.getFileName('gif');
    download(blob, fileName, 'gif');
  }

  getFileName(extension) {
    let name = '';
    if (this.fileName.length > 0) {
      name = `${this.fileName}.${extension}`;
    } else {
      randomString = getUniqueString();
      name = name = `${randomString}.${extension}`;
    }
    return name;
  }

  saveAPNG() {
    const imgDataStack = [];
    const frameStack = this.getFrameStack();
    const mainCanvas = document.getElementById('canvas');
    frameStack.forEach(element => {
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      canvas.width = mainCanvas.width;
      canvas.height = mainCanvas.height;
      const img = new Image;
      img.src = element;
      context.drawImage(img,0,0);
      const imgData = context.getImageData(0, 0, canvas.width, canvas.height).data.buffer;
      imgDataStack.push(imgData);
    });
    const delayArr = new Array(frameStack.length).fill(1000 / this.getFPS());
    const resultUPNG = UPNG.encode(imgDataStack, mainCanvas.width, mainCanvas.height, 0, delayArr);
    const fileName = `${this.fileName}.apng`;
    download(resultUPNG, fileName, 'apng');
  }

  createDownloadControl() {
    const downloadControlElement = document.createElement('div');
    const textInput = document.createElement('input');
    textInput.type = 'text';
    textInput.addEventListener('input', this.setFileName);
    const gifButton = document.createElement('button');
    gifButton.innerHTML = 'GIF';
    gifButton.addEventListener('click', this.saveGIF);
    const apngButton = document.createElement('button');
    apngButton.innerHTML = 'APNG';
    apngButton.addEventListener('click', this.saveAPNG);
    downloadControlElement.appendChild(textInput);
    downloadControlElement.appendChild(apngButton);
    downloadControlElement.appendChild(gifButton);
    return downloadControlElement;
  }
}