import * as firebase from "firebase/app";
import "firebase/auth";
import FIREBASE_CONFIG from './firebaseConfig';

export default class Authenticator {
  constructor() {
    this.signOut = this.signOut.bind(this);
    this.authenticate = this.authenticate.bind(this);
    this.firebaseConfig = FIREBASE_CONFIG;
  }

 async authenticate() {
    const provider = new firebase.auth.GoogleAuthProvider();
    const user = await firebase.auth().signInWithPopup(provider).then(function(result) {
      return result.user;
    });
    this.processVisibility(user.displayName);
  }

  signOut() {
    firebase.auth().signOut();
    this.processVisibility();
  }

  processVisibility(nameplateText) {
    const namePlate = document.getElementById('nameplate');
    if (nameplateText) {
      namePlate.innerHTML = `Signed in as: ${nameplateText}`;
    } else {
      namePlate.innerHTML = '';
    };
    const authenticateButton = document.getElementById('authenticate');
    authenticateButton.classList.toggle('invisible');
    const signOutButton = document.getElementById('signout');
    signOutButton.classList.toggle('invisible');
  }

  createAuthenticateButton() {
    firebase.initializeApp(this.firebaseConfig);
    const authenticateButton = document.createElement('button');
    authenticateButton.addEventListener('click', this.authenticate);
    authenticateButton.innerHTML = '<i class="fab fa-google"></i> Sign in';
    authenticateButton.id = 'authenticate';
    const signOutButton = document.createElement('button');
    signOutButton.addEventListener('click', this.signOut);
    signOutButton.innerHTML = '<i class="fab fa-google"></i> Sign out';
    signOutButton.id = 'signout';
    signOutButton.classList.toggle('invisible');
    const namePlate = document.createElement('div');
    namePlate.classList.add('header__nameplate');
    namePlate.id = 'nameplate';
    const authenticatorContainer = document.createElement('div');
    authenticatorContainer.appendChild(authenticateButton);
    authenticatorContainer.appendChild(signOutButton);
    authenticatorContainer.appendChild(namePlate);
    return authenticatorContainer;
  }
}