import '../assets/css/reset.css';
import '../assets/css/style.css';
import Canvas from './canvas/index';
import Palette from './palette/index';
import Toolbox from './toolbox/index';
import AnimationPreview from './animationPreview/index';
import FramesContainer from './framesContainer/index';
import DownloadControl from './downloadControl/index';
import Authenticator from './authenticator/index';
import { INITIAL_CONFIG } from '../utils/constants';
import setActiveBlockElement from '../utils/setActiveBlockElement';
import setElementColor from '../utils/setElementColor';

export default class App {
  constructor() {
    this.color = INITIAL_CONFIG.initialColor;
    this.previousColor = INITIAL_CONFIG.previousColor;
    this.pixelSize = INITIAL_CONFIG.pixelSize;
    this.pixelCount = INITIAL_CONFIG.pixelCount;
    this.activeTool = INITIAL_CONFIG.activeTool;
    this.getColor = this.getColor.bind(this);
    this.setColor = this.setColor.bind(this);
    this.getPreviousColor = this.getPreviousColor.bind(this);
    this.setPreviousColor = this.setPreviousColor.bind(this);
    this.getActiveTool = this.getActiveTool.bind(this);
    this.setActiveTool = this.setActiveTool.bind(this);
    this.getPixelSize = this.getPixelSize.bind(this);
    this.setPixelSize = this.setPixelSize.bind(this);
    this.getPixelCount = this.getPixelCount.bind(this);
    this.setPixelCount = this.setPixelCount.bind(this);
    this.getFrameStack = this.getFrameStack.bind(this);
    this.setFrameStack = this.setFrameStack.bind(this);
    this.getFPS = this.getFPS.bind(this);
    this.setFPS = this.setFPS.bind(this);
    this.processLoad = this.processLoad.bind(this);
    this.processUnload = this.processUnload.bind(this);
    this.canvas = null;
    this.palette = null;
    this.toolbox = null;
    this.framesContainer = null;
    this.animationPreview = null;
    this.downloadControl = null;
    this.authenticateButton = null;
    this.frameStack = [];
    this.fps = 0;
  }

  setColor(color) {
    setElementColor('#current', color);
    this.color = color;
  }

  getPreviousColor() {
    return this.previousColor;
  }

  setPreviousColor(color) {
    setElementColor('#previous', color);
    this.previousColor = color;
  }

  getColor() {
    return this.color;
  }

  getActiveTool() {
    return this.activeTool;
  }

  setActiveTool(activeTool) {
    this.activeTool = activeTool;
  }

  getPixelCount() {
    return this.pixelCount;
  }

  setPixelCount(pixelCount) {
    this.pixelCount = pixelCount;
  }

  getPixelSize() {
    return this.pixelSize;
  }

  setPixelSize(pixelSize) {
    this.pixelSize = +pixelSize;
  }

  getFrameStack() {
    return this.frameStack;
  }

  setFrameStack(frameStack) {
    this.frameStack = frameStack;
  }

  getFPS() {
    return this.fps;
  }

  setFPS(fps) {
    this.fps = fps;
  }

  processLoad() {
    const canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');
    const storageActiveTool = localStorage.getItem('activeTool');
    if (storageActiveTool) {
      this.activeTool = storageActiveTool;
    }
    const storageColor = localStorage.getItem('color');
    if (storageColor) {
      this.color = storageColor;
    }
    const storagePixelSize = localStorage.getItem('pixelSize');
    if (storagePixelSize) {
      this.pixelSize = +storagePixelSize;
      document.getElementById('size').value = storagePixelSize;
    }
    const storagePixelCount = localStorage.getItem('pixelCount');
    if (storagePixelCount) {
      this.pixelCount = storagePixelCount;
      canvas.width = storagePixelCount;
      canvas.height = storagePixelCount;
      document.getElementById('canvas-size').value = storagePixelCount;
    }
    const imageData = localStorage.getItem('imageData');
    if (imageData) {
      const img = new Image();
      img.src = imageData;
      img.addEventListener('load', () => {
        context.drawImage(img, 0, 0);
      });
    }
    const element = document.querySelector(`#${this.activeTool}`);
    setActiveBlockElement(element);
    document.querySelector('#primary-color').value = this.color;
    setElementColor('#current', this.color);
    setElementColor('#previous', this.previousColor);
  }

  processUnload() {
    if (localStorage.getItem('innerHtml')) {
      localStorage.removeItem('innerHtml');
      localStorage.removeItem('activeTool');
      localStorage.removeItem('color');
      localStorage.removeItem('pixelSize');
      localStorage.removeItem('pixelCount');
      localStorage.removeItem('pixelState');
      localStorage.removeItem('imageData');
    } else {
      const htmlContents = document.documentElement.innerHTML;
      localStorage.setItem('innerHtml', htmlContents);
      localStorage.setItem('activeTool', this.activeTool);
      localStorage.setItem('color', this.color);
      localStorage.setItem('pixelSize', this.pixelSize);
      localStorage.setItem('pixelCount', this.pixelCount);
      const canvas = document.getElementById('canvas');
      localStorage.setItem('imageData', canvas.toDataURL());
    }
  }

  start() {
    const main = document.createElement('div');
    main.classList.add('main');
    const controlBlocks = document.createElement('div');
    controlBlocks.classList.add('main__controls');
    const animationBlock = document.createElement('div');
    animationBlock.classList.add('main__animation');
    this.toolbox = new Toolbox(this.setActiveTool);
    controlBlocks.appendChild(this.toolbox.createToolbox());
    this.palette = new Palette(this.setColor, this.setPreviousColor, this.getColor);
    controlBlocks.appendChild(this.palette.createPalette());
    main.appendChild(controlBlocks);
    this.canvas = new Canvas(this.getColor, this.getActiveTool,
      this.getPixelSize, this.getPixelCount, this.setColor,
      this.setPixelSize, this.setPixelCount, this.setPreviousColor);
    this.framesContainer = new FramesContainer(this.getFrameStack, this.setFrameStack, this.getPixelCount);
    main.appendChild(this.framesContainer.createFramesContainer());
    main.appendChild(this.canvas.createCanvas());
    this.animationPreview = new AnimationPreview(this.getFrameStack, this.getFPS, this.setFPS);
    animationBlock.appendChild(this.animationPreview.createAnimationPreview());
    this.downloadControl = new DownloadControl(this.getFrameStack, this.getFPS);
    animationBlock.appendChild(this.downloadControl.createDownloadControl());
    const header = document.querySelector('header');
    this.authenticateButton = new Authenticator();
    header.appendChild(this.authenticateButton.createAuthenticateButton());
    main.appendChild(animationBlock);
    document.querySelector('body').appendChild(main);
    window.addEventListener('load', this.processLoad);
    window.addEventListener('unload', this.processUnload);
  }
}
