import { PALETTE } from '../../utils/constants';
import createControlBlockElement from '../../utils/createControlBlockElement';
import rgbToHex from '../../utils/rgbToHex';

export default class Palette {
  constructor(setColor, setPreviousColor, getColor) {
    this.setColor = (event) => {
      let color = '';
      const primaryColorElement = document.querySelector('#primary-color');
      if (event.target.type === 'color') {
        color = event.target.value;
      } else {
        color = event.target.parentElement.children[0].children[0].style.background;
        color = rgbToHex(color);
        primaryColorElement.value = color;
      }
      if (getColor() !== color) {
        setPreviousColor(getColor());
      }
      setColor(color);
    };
  }

  createPalette() {
    const palette = document.createElement('div');
    Object.keys(PALETTE).forEach((key) => {
      const paletteKey = PALETTE[key];
      const item = createControlBlockElement(paletteKey.name, paletteKey.icon,
        paletteKey.text, 'palette');
      item.addEventListener('click', this.setColor);
      item.children[0].children[0].children[0].style.background = paletteKey.color;
      palette.appendChild(item);
    });

    palette.id = 'palette';
    palette.classList.add('main__palette', 'control-block', 'palette');
    const primaryColor = document.createElement('input');
    primaryColor.type = 'color';
    primaryColor.id = 'primary-color';
    primaryColor.addEventListener('change', this.setColor);
    palette.appendChild(primaryColor);
    return palette;
  }
}
