/* eslint-disable no-param-reassign */
/* eslint-disable func-names */
/* eslint-disable no-mixed-operators */
/* eslint-disable no-plusplus */
/* eslint-disable class-methods-use-this */
import { TOOLS, CANVAS_CONFIG } from '../../utils/constants';
import rgbToHex from '../../utils/rgbToHex';
import hexToRgb from '../../utils/hexToRgb';
import createRangeInput from '../../utils/createRangeInput';

export default class Canvas {
  constructor(getColor, getActiveTool, getPixelSize, getPixelCount, setColor,
    setPixelSize, setPixelCount, setPreviousColor) {
    this.getColor = getColor;
    this.setPreviousColor = setPreviousColor;
    this.getActiveTool = getActiveTool;
    this.getPixelSize = getPixelSize;
    this.getPixelCount = getPixelCount;
    this.setColor = setColor;
    this.addPixel = this.addPixel.bind(this);
    this.setPaint = this.setPaint.bind(this);
    this.erasePixel = this.erasePixel.bind(this);
    this.paintBucket = this.paintBucket.bind(this);
    this.eventHandler = this.eventHandler.bind(this);
    this.floodFill = this.floodFill.bind(this);
    this.pickColor = this.pickColor.bind(this);
    this.replaceColor = this.replaceColor.bind(this);
    this.changePixelColor = this.changePixelColor.bind(this);
    this.fillRequredPixels = this.fillRequredPixels.bind(this);
    this.paint = false;
    this.handlers_map = {
      [TOOLS.PENCIL.name]: this.addPixel,
      [TOOLS.ERASER.name]: this.erasePixel,
      [TOOLS.BUCKET.name]: this.paintBucket,
      [TOOLS.PICKER.name]: this.pickColor,
      [TOOLS.REPLACE.name]: this.replaceColor,
    };
    this.setPixelSizeOuter = setPixelSize;
    this.setPixelCountOuter = setPixelCount;
    this.setPixelCount = this.setPixelCount.bind(this);
    this.setPixelSize = this.setPixelSize.bind(this);
    this.startX = 0;
    this.startY = 0;
  }

  setPixelSize(event) {
    const pixelSize = event.target.value;
    this.setPixelSizeOuter(pixelSize);
  }

  setPixelCount(event) {
    const pixelCount = +event.target.value;
    const canvas = document.getElementById('canvas');
    const canvasContext = canvas.getContext('2d');
    const fakeCanvas = document.createElement('canvas');
    const fakeCanvasContext = fakeCanvas.getContext('2d');
    fakeCanvas.width = 512;
    fakeCanvas.height = 512;
    fakeCanvasContext.imageSmoothingEnabled = false;
    fakeCanvasContext.drawImage(canvas, 0, 0, fakeCanvas.width, fakeCanvas.height);
    canvas.width = pixelCount;
    canvas.height = pixelCount;
    canvasContext.imageSmoothingEnabled = false;
    canvasContext.drawImage(fakeCanvas, 0, 0, canvas.width, canvas.height);
    this.setPixelCountOuter(pixelCount);
  }

  setPaint(event) {
    if (this.paint) {
      this.paint = false;
    } else {
      this.paint = true;
    }
    const coords = this.getPixelCoords(event);
    this.startX = coords.X;
    this.startY = coords.Y;
  }

  getPixelCoords(event) {
    const pixelCount = this.getPixelCount();
    const width = +CANVAS_CONFIG.width;
    const X = Math.floor((event.pageX - event.target.offsetLeft) * pixelCount
    / width);
    const Y = Math.floor((event.pageY - event.target.offsetTop) * pixelCount
    / width);
    return { X, Y };
  }

  addPixel(event) {
    if (this.checkForContinuousDrawing(event)) return;
    this.changePixelColor(event, this.getColor());
  }

  erasePixel(event) {
    if (this.checkForContinuousDrawing(event)) return;
    this.changePixelColor(event, '#ffffff');
  }

  fillRequredPixels(x, y, color) {
    const pixelSize = this.getPixelSize();
    const context = document.getElementById('canvas').getContext('2d');
    for (let i = x; i < x + pixelSize; i++) {
      for (let j = y; j < y + pixelSize; j++) {
        if (i >= 0 && j >= 0) {
          context.fillStyle = color;
          context.imageSmoothingEnabled = false;
          context.fillRect(i, j, 1, 1);
        }
      }
    }
  }

  changePixelColor(event, color) {
    let x1 = this.startX;
    let y1 = this.startY;
    const coords = this.getPixelCoords(event);
    const x2 = coords.X;
    const y2 = coords.Y;
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const signX = x1 < x2 ? 1 : -1;
    const signY = y1 < y2 ? 1 : -1;
    let error = deltaX - deltaY;
    this.fillRequredPixels(x2, y2, color);
    while (x1 !== x2 || y1 !== y2) {
      this.fillRequredPixels(x1, y1, color);
      const error2 = error * 2;
      if (error2 > -deltaY) {
        error -= deltaY;
        x1 += signX;
      }
      if (error2 < deltaX) {
        error += deltaX;
        y1 += signY;
      }
    }
    this.startX = x2;
    this.startY = y2;
  }

  checkPixelColor(event) {
    const canvas = document.getElementById('canvas');
    const ctx = canvas.getContext('2d');
    const coords = this.getPixelCoords(event);
    const { X, Y } = coords;
    const pixel = ctx.getImageData(X, Y, 1, 1);
    const { data } = pixel;
    const rgb = {
      r: data[0],
      g: data[1],
      b: data[2],
    };
    return rgb;
  }

  pickColor(event) {
    if (event.type !== 'click') return;
    const rgb = this.checkPixelColor(event);
    const rgbString = `rgb(${rgb.r}, ${rgb.g}, ${rgb.b})`;
    const hexColor = rgbToHex(rgbString);
    this.setPreviousColor(this.getColor());
    this.setColor(hexColor);
  }

  floodFill(event) {
    const coords = this.getPixelCoords(event);
    const { X, Y } = coords;
    const canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');
    const drawingBoundTop = 0;
    const { width } = canvas;
    const imageData = context.getImageData(0, 0, width, width);
    const pixelStack = [[X, Y]];
    const curColor = hexToRgb(this.getColor());
    const colorToReplace = this.checkPixelColor(event);
    while (pixelStack.length) {
      const newPos = pixelStack.pop();
      const quadWidth = width * 4;
      let pixelPos;
      let reachLeft;
      let reachRight;
      const [x] = newPos;
      let y = newPos[1];
      pixelPos = (y * width + x) * 4;
      while (y-- >= drawingBoundTop
        && this.matchStartColor(pixelPos, curColor, colorToReplace, imageData)) {
        pixelPos -= quadWidth;
      }
      pixelPos += quadWidth;
      ++y;
      reachLeft = false;
      reachRight = false;
      while (y++ < width - 1
        && this.matchStartColor(pixelPos, curColor, colorToReplace, imageData)) {
        this.colorPixel(pixelPos, curColor, imageData);
        if (x > 0) {
          if (this.matchStartColor(pixelPos - 4, curColor, colorToReplace, imageData)) {
            if (!reachLeft) {
              pixelStack.push([x - 1, y]);
              reachLeft = true;
            }
          } else if (reachLeft) {
            reachLeft = false;
          }
        }
        if (x < width - 1) {
          if (this.matchStartColor(pixelPos + 4, curColor, colorToReplace, imageData)) {
            if (!reachRight) {
              pixelStack.push([x + 1, y]);
              reachRight = true;
            }
          } else if (reachRight) {
            reachRight = false;
          }
        }
        pixelPos += quadWidth;
      }
    }
    context.putImageData(imageData, 0, 0);
  }

  matchStartColor(pixelPos, curColor, colorToReplace, imageData) {
    const r = imageData.data[pixelPos];
    const g = imageData.data[pixelPos + 1];
    const b = imageData.data[pixelPos + 2];

    return (r !== curColor.r || g !== curColor.g || b !== curColor.b)
    && (r === colorToReplace.r && g === colorToReplace.g && b === colorToReplace.b);
  }

  colorPixel(pixelPos, curColor, imageData) {
    imageData.data[pixelPos] = curColor.r;
    imageData.data[pixelPos + 1] = curColor.g;
    imageData.data[pixelPos + 2] = curColor.b;
    imageData.data[pixelPos + 3] = 255;
  }

  paintBucket(event) {
    if (this.checkForContinuousDrawing(event)) return;
    this.floodFill(event);
  }

  replaceColor(event) {
    if (this.checkForContinuousDrawing(event)) return;
    const curColor = hexToRgb(this.getColor());
    const colorToReplace = this.checkPixelColor(event);
    const canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');
    const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
    const imageDataPixelOffset = 4;
    for (let i = 0; i < imageData.data.length; i += imageDataPixelOffset) {
      if (imageData.data[i] === colorToReplace.r
        && imageData.data[i + 1] === colorToReplace.g
        && imageData.data[i + 2] === colorToReplace.b
      ) {
        imageData.data[i] = curColor.r;
        imageData.data[i + 1] = curColor.g;
        imageData.data[i + 2] = curColor.b;
      }
    }
    context.putImageData(imageData, 0, 0);
  }

  checkForContinuousDrawing(event) {
    return !!((this.paint === false && event.type === 'mousemove'));
  }

  eventHandler(event) {
    const handlerName = TOOLS[this.getActiveTool()].name;
    return this.handlers_map[handlerName](event);
  }

  createCanvas() {
    const canvasContainer = document.createElement('div');
    canvasContainer.classList.toggle('canvas-container');
    const canvas = document.createElement('canvas');
    canvas.id = CANVAS_CONFIG.name;
    canvas.addEventListener('click', this.eventHandler);
    canvas.addEventListener('mousedown', this.setPaint);
    canvas.addEventListener('mouseup', this.setPaint);
    canvas.addEventListener('mousemove', this.eventHandler);
    canvas.addEventListener('mouseleave', () => { this.paint = false; });
    canvas.style.width = `${CANVAS_CONFIG.width}px`;
    canvas.style.height = `${CANVAS_CONFIG.height}px`;
    canvas.width = CANVAS_CONFIG.pixels;
    canvas.height = CANVAS_CONFIG.pixels;
    canvasContainer.appendChild(canvas);
    canvasContainer.appendChild(createRangeInput('size', 'Brush size in pixels: ', 1, 4, 4, 1, this.setPixelSize));
    canvasContainer.appendChild(createRangeInput('canvas-size', 'Canvas size in pixels: ', 32, 128, 32, 32, this.setPixelCount));
    return canvasContainer;
  }
}
