export default class FramesContainer {
  constructor(getFrameStack, setFrameStack, getPixelCount) {
    this.getFrameStack = getFrameStack;
    this.setFrameStack = setFrameStack;
    this.getPixelCount = getPixelCount;
    this.createFrame = this.createFrame.bind(this);
    this.addNewFrame = this.addNewFrame.bind(this);
    this.framesContainer = null;
    this.duplicateFrame = this.duplicateFrame.bind(this);
    this.deleteFrame = this.deleteFrame.bind(this);
    this.getIndexOfFrameToOperate = this.getIndexOfFrameToOperate.bind(this);
  }

  getCurrentCanvasDataURL() {
    const canvasElement = document.getElementById('canvas');
    return canvasElement.toDataURL();
  }

  addNewFrame() {
    const dataUrl = this.getCurrentCanvasDataURL();
    const frameStack = [...this.getFrameStack()];
    frameStack.push(dataUrl);
    this.setFrameStack(frameStack);
    const frame = this.createFrame(dataUrl);
    this.framesContainer.appendChild(frame);
  }

  duplicateFrame(event) {
    const frameStack = [...this.getFrameStack()];
    const target = event.target.parentElement;
    const parent = target.parentElement;
    const canvasToDuplicate = target.previousElementSibling;
    const dataURL = canvasToDuplicate.toDataURL();
    const frame = this.createFrame(dataURL);
    const frameIndex = this.getIndexOfFrameToOperate(parent);
    const indexToInsertAt = frameIndex + 1;
    this.framesContainer.insertBefore(frame, this.framesContainer.children[indexToInsertAt]);
    frameStack.splice(frameIndex, 0, dataURL);
    this.setFrameStack(frameStack);
  }

  getIndexOfFrameToOperate(frame) {
    return Array.prototype.indexOf.call(this.framesContainer.children, frame);
  }

  deleteFrame(event) {
    const target = event.target.parentElement;
    const parent = target.parentElement;
    const indexToDelete = this.getIndexOfFrameToOperate(parent);
    const frames = [...this.getFrameStack()];
    frames.splice(indexToDelete, 1);
    this.setFrameStack(frames);
    this.framesContainer.removeChild(parent);
  }

  createFrame(dataUrl) {
    const frameElement = document.createElement('li');
    const frameCanvas = document.createElement('canvas');
    frameCanvas.style.width = '100px';
    frameCanvas.style.height = '100px';
    const pixelCount = this.getPixelCount();
    frameCanvas.width = pixelCount;
    frameCanvas.height = pixelCount;
    const duplicateButon = document.createElement('button');
    duplicateButon.addEventListener('click', this.duplicateFrame);
    const deleteButton = document.createElement('button');
    deleteButton.addEventListener('click', this.deleteFrame);
    duplicateButon.innerHTML = '<i class="far fa-clone"></i>';
    deleteButton.innerHTML = '<i class="fas fa-trash-alt"></i>';
    frameElement.appendChild(frameCanvas);
    frameElement.appendChild(duplicateButon);
    frameElement.appendChild(deleteButton);

    const context = frameCanvas.getContext('2d');
    const img = new Image;
    img.onload = function(){
      context.drawImage(img,0,0);
    };
    img.src = dataUrl;

    return frameElement;
  }

  createFramesContainer() {
    const framesWrapperElement = document.createElement('div');
    this.framesContainer = document.createElement('ul');
    const frameStack = this.getFrameStack();
    frameStack.forEach((element) => {
      const frame = this.createFrame(element);
      this.framesContainer.appendChild(frame);
    });
    framesWrapperElement.appendChild(this.framesContainer);
    const addFrameButton = document.createElement('button');
    addFrameButton.addEventListener('click', this.addNewFrame);
    addFrameButton.innerText = 'Add new frame';
    framesWrapperElement.appendChild(addFrameButton);
    return framesWrapperElement;
  }
}
