import createRangeInput from '../../utils/createRangeInput';

export default class AnimationPreview {
  constructor(getFrameStack, getFPS, setFPS) {
    this.getFrameStack = getFrameStack;
    this.getFPS = getFPS;
    this.setFPS = setFPS;
    this.onFPSchange = this.onFPSchange.bind(this);
    this.startTime = null;
    this.fpsInterval = 0;
    this.currentImageIndex = 0;
    this.startAnimation = this.startAnimation.bind(this);
    this.animate = this.animate.bind(this);
    this.stopAnimation = true;
    this.stopAnimatedPreview = this.stopAnimatedPreview.bind(this);
  }

  onFPSchange(event) {
    const fps = event.target.value;
    this.setFPS(fps);
  }

  startAnimation() {
    const frameStack = this.getFrameStack();
    if (frameStack.length === 0) return;
    this.startTime = Date.now();
    this.stopAnimation = false;
    this.animate();
  }

  animate() {
    if (this.stopAnimation) {
      return;
    }
    requestAnimationFrame(this.animate);
    this.fpsInterval = 1000 / this.getFPS();
    const now = Date.now();
    const elapsed = now - this.startTime;

    if (elapsed > this.fpsInterval) {
      this.startTime = now - (elapsed % this.fpsInterval);
      const canvas = document.getElementById('animation');
      const context = canvas.getContext('2d');
      context.clearRect(0, 0, canvas.width, canvas.height);
      const frameStack = this.getFrameStack();
      const img = new Image;
      img.onload = function(){
        context.drawImage(img,0,0);
      };
      img.src = frameStack[this.currentImageIndex];
      if (this.currentImageIndex === frameStack.length - 1) {
        this.currentImageIndex = 0;
      } else {
        this.currentImageIndex += 1;
      }
    }
  }

  stopAnimatedPreview() {
    this.stopAnimation = true;
  }

  toggleFullscreenMode() {
    if (document.fullscreenEnabled) {
      document.getElementById('animation').requestFullscreen();
    }
  }

  createAnimationPreview() {
    const previewWrapperElement = document.createElement('div');
    const startButton = document.createElement('button');
    startButton.innerHTML = '<i class="far fa-play-circle"></i>';
    startButton.addEventListener('click', this.startAnimation);
    const stopButton = document.createElement('button');
    stopButton.innerHTML = '<i class="far fa-stop-circle"></i>';
    stopButton.addEventListener('click', this.stopAnimatedPreview);
    const fullScreenButton = document.createElement('button');
    fullScreenButton.innerHTML = '<i class="fas fa-tv"></i>';
    fullScreenButton.addEventListener('click', this.toggleFullscreenMode);
    const previewCanvas = document.createElement('canvas');
    previewCanvas.id = 'animation';
    const fpsInput = createRangeInput('fps', 'FPS:', 0, 24, 0, 1, this.onFPSchange);
    previewWrapperElement.appendChild(previewCanvas);
    previewWrapperElement.appendChild(fullScreenButton);
    previewWrapperElement.appendChild(startButton);
    previewWrapperElement.appendChild(stopButton);
    previewWrapperElement.appendChild(fpsInput);
    return previewWrapperElement;
  }
}