/* eslint-disable class-methods-use-this */
/* eslint-disable consistent-return */
/* eslint-disable func-names */
import { TOOLS, KEY_BINDS } from '../../utils/constants';
import createControlBlockElement from '../../utils/createControlBlockElement';
import setActiveBlockElement from '../../utils/setActiveBlockElement';

export default class Toolbox {
  constructor(setActiveTool) {
    this.keybindActivation = this.keybindActivation.bind(this);
    this.setActiveToolOuter = setActiveTool;
    this.setActiveTool = this.setActiveTool.bind(this);
  }

  setActiveTool(event) {
    let activeTool = null;
    const parent = event.target.parentElement.parentElement;
    if (event.type === 'click' && parent.tagName === 'BUTTON') {
      activeTool = TOOLS[`${parent.id}`].name;
      setActiveBlockElement(parent);
    } else {
      switch (event.which) {
        case KEY_BINDS.P:
          activeTool = this.keybindActivation(TOOLS.PENCIL);
          break;
        case KEY_BINDS.E:
          activeTool = this.keybindActivation(TOOLS.ERASER);
          break;
        case KEY_BINDS.B:
          activeTool = this.keybindActivation(TOOLS.BUCKET);
          break;
        case KEY_BINDS.C:
          activeTool = this.keybindActivation(TOOLS.PICKER);
          break;
        case KEY_BINDS.R:
          activeTool = this.keybindActivation(TOOLS.REPLACE);
          break;
        default:
          return null;
      }
    }
    this.setActiveToolOuter(activeTool);
  }

  keybindActivation(tool) {
    const toolName = tool.name;
    const element = document.querySelector(`#${toolName}`);
    setActiveBlockElement(element);
    return toolName;
  }

  createToolbox() {
    const toolbox = document.createElement('div');
    Object.keys(TOOLS).forEach((key) => {
      const toolKey = TOOLS[key];
      const tool = createControlBlockElement(toolKey.name, toolKey.icon, toolKey.text, 'toolbox');
      tool.addEventListener('click', this.setActiveTool);
      toolbox.appendChild(tool);
    });

    toolbox.id = 'toolbox';
    toolbox.classList.add('main__toolbox', 'control-block', 'toolbox');
    document.querySelector('body').addEventListener('keypress', this.setActiveTool);
    return toolbox;
  }
}
