export default function createControlBlockElement(id, icon, elementText, parentName) {
  const element = document.createElement('button');
  element.classList.add(`${parentName}__button`, 'control-button');
  element.id = id;
  element.innerHTML = `<div class="control-button__container">
        <div class="control-button__icon">
            ${icon}
        </div>
        <div class="control-button__text">
            ${elementText}
        </div>
    </div>`;
  return element;
}
